@font-face {
	font-family: DefaultBlack;
	src: url(/public/fonts/ProductSans-Black.ttf);
}

@font-face {
	font-family: DefaultBold;
	src: url(/public/fonts/ProductSans-Bold.ttf);
}

@font-face {
	font-family: DefaultMedium;
	src: url(/public/fonts/ProductSans-Medium.ttf);
}

@font-face {
	font-family: DefaultRegular;
	src: url(/public/fonts/ProductSans-Light.ttf);
}

@tailwind base;
@tailwind components;
@tailwind utilities;

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html, body {
	background: #ffffff;
	overflow: hidden;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

a, button {
	text-decoration: none;
	color: inherit;
	-webkit-tap-highlight-color: transparent;
	border: 0;
	outline: 0;
	background: transparent;
}

input, textarea {
	border: 0;
	background: transparent;
	outline: 0;
}

.scrollbar-hidden::-webkit-scrollbar {
	display: none;
}

.scrollbar-hidden {
	-ms-overflow-style: none;
	scrollbar-width: none;
}

.square::before {
	content: "";
	display: block;
	width: 100%;
	padding-bottom: 100%;
	position: relative;
	z-index: -1;
}

.top-fade {
	background: linear-gradient(to bottom, #ffffff, transparent);
}

.bottom-fade {
	background: linear-gradient(to top, #ffffff, transparent);
}

.animate-line-up {
	transform: translateY(-100%);
}

.light {
	font-family: DefaultRegular, sans-serif;
	font-size: 70%;
}